import * as React from 'react';
import Section from '../components/Section';

import { Image, Row, Col, Card, Statistic } from 'antd'; 

import fisk from './media/fisk.jpg';

export default function Boats() {

    var prices = (
        <Card title="Prisar fiskekort for Hornindalsvatnet" bordered={false}>
            <Row gutter={[32,32]}>
                <Col xs={12} lg={8}><Statistic title="Dgnkort" value="50,-" /></Col>
                <Col xs={12} lg={8}><Statistic title="Sesongkort" value="300,-"/></Col>
                <Col xs={24} lg={8}><Statistic title="rlig forvaltningskort" value="20,-" /></Col>
            </Row>
        </Card>
    );

    return (
        <Section id="fishing" title="Fiske">
            <Row gutter={16}>
                <Col xs={24} lg={16}>
                    <p>P Nesjartun har du gode mogligheitar til  fiske bde i vatn, elv og sj. Vi sel fiskekort for Hornindalsvatnet og vre del av Eidselva, sone Nor. I tillegg til fiskekort, m du ogs kjpe forvaltningskort p <a href="elveguiden.no" target="_blank">elveguiden.no</a>.</p>
                    {prices}
                </Col>
                <Col xs={0} lg={8}>
                    <Image src={fisk} preview={false} style={{ marginBottom: "16px" }} width="100%"/>
                </Col>
            </Row>
            <Row gutter={16}>
                <Col xs={24} lg={16}>
                    <p>For nrmare info om laksefiske i eidselva sj <a href="www.eidselva.no" target="_blank">eidselva.no</a></p>
                </Col>
            </Row>
            
        </Section>
    );
}