import * as React from 'react';
import Section from '../components/Section';

import { Image, Row, Col, Card, Statistic } from 'antd'; 

import brygge from './media/brygge.jpg';

export default function Boats() {

    var prices = (
        <Card title="Prisar" bordered={false}>
            <Row gutter={[32, 32]}>
                <Col xs={8}  lg={4}><Statistic title="Robt per time" value="60,-" /></Col>
                <Col xs={8}  lg={4}><Statistic title="Trbt per time" value="60,-" /></Col>
                <Col xs={8} lg={4}><Statistic title="Kano per time" value="60,-" /></Col>
                <Col xs={8} lg={4}><Statistic title="Motorbt per time" value="120,-" /></Col>
                <Col xs={16} lg={8}><Statistic title="Motorbt per dag" value="400,-" suffix="+ bensin" /></Col>
            </Row>
        </Card>
    );

    return (
        <Section id="boats" title="Btutleige">
            <Image src={brygge} preview={false} width="100%" style={{ marginBottom: "16px" }} />
            <p>P Nesjartun har du mulighet til  leige bt p Hornindalsvatnet. Vi har motorbtar, ein robt, trbt og kano.</p>
            {prices}
        </Section>
    );
}