import * as React from 'react';
import Section from '../components/Section';
import { Card, Col, Image, Row, Statistic } from 'antd';

import camping from './media/camping.jpg';

export default function Camping() {

    var prices = (
        <Card title="Prisar" bordered={false} style={{ margin: "15px 5px" }}>
            <Row gutter={[16,32]}>
                <Col xs={12} lg={8}><Statistic title="Bobil inkl. 4 pers." value="240,-" /></Col>
                <Col xs={12} lg={8}><Statistic title="Caravan og bil inkl. 4 pers." value="270,-" /></Col>
                <Col xs={12} lg={8}><Statistic title="Telt og bil inkl. 4 pers" value="220,-" /></Col>
                <Col xs={12} lg={8}><Statistic title="Telt og MV inkl. 2 pers." value="200,-" /></Col>
                <Col xs={12} lg={8}><Statistic title="Telt aleine inkl. 2 pers." value="170,-" /></Col>
                <Col xs={12} lg={8}><Statistic title="Straum" value="30,-" /></Col>
            </Row>
        </Card>
    );

    var additional = (
        <Card title="Tillegg for fleire personar" bordered={false} style={{ margin: "15px 5px" }}>
            <Row gutter={[16, 32]}>
                <Col span={12}><Statistic title="Vaksen" value="20,-" /></Col>
                <Col span={12}><Statistic title="Barn" value="10,-" /></Col>
            </Row>
        </Card>
    );

    return (
        <Section id="camping" title="Telt, bil og vogn">
            <Image src={camping} preview={false} style={{ marginBottom: "16px" }} width="100%" />
            <p>P campingplassen har vi plass til ca 25 bubilar, vogner og telt. Vi har delt plassen opp i 4 plat, slik at du alltid har god utsikt utover vatnet.</p>
            <p>24 straumuttak, Saniteranlegg med dusj, wc og vaskerom. Det er og mogleg  leige vaskemaskin.</p>
            <Row gutter={16}>
                <Col sm={24} lg={18}>{prices}</Col>
                <Col sm={24} lg={6}>{additional}</Col>
            </Row>
        </Section>
    );
}